// --- SUB-COMPONENTS ---

const NavItem = ({
  label,
  active,
  onMouseEnter,
}: {
  label: string;
  active: boolean;
  onMouseEnter: () => void;
}) => (
  <div
    onMouseEnter={onMouseEnter}
    className="relative h-full flex items-center cursor-pointer px-4 group"
  >
    <span
      className={cn(
        "text-sm font-semibold uppercase tracking-wide transition-colors duration-200 flex items-center gap-1",
        active ? "text-blue-700" : "text-slate-600 group-hover:text-blue-700"
      )}
    >
      {label}
      <ChevronDown
        className={cn(
          "w-4 h-4 transition-transform duration-300",
          active ? "rotate-180 text-blue-600" : "rotate-0 text-slate-400"
        )}
      />
    </span>
    {/* Colored indicator line */}
    <div
      className={cn(
        "absolute bottom-0 left-0 w-full h-[2px] bg-blue-600 transition-transform duration-300 origin-left",
        active ? "scale-x-100" : "scale-x-0"
      )}
    />
    {/* Invisible bridge */}
    <div className="absolute bottom-0 left-0 w-full h-6 translate-y-full bg-transparent" />
  </div>
);

